body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}





.search {
  text-align: center;
  padding: 8px;
  height: 50px;
}

.search-result {
  text-align: center;
}


.content {
  display: grid;
  grid-template-columns: 1fr;
  
}
/* #col-left {
  min-width: 700px;
} */

.error-block {
  text-align: center;
}

.wrapper {
  display: grid;
  grid-template-columns: 670px 1fr;
  grid-template-rows: auto auto; 
  grid-gap: 20px;
  margin-top: 20px;
}

.mainVideo {
 grid-row: 1 / 2;
 grid-column: 1 / 2;
}

.videosList {
 grid-row: 1 / 3;
 grid-column: 2 / 3;
}

.comments {
  grid-row: 2 / 3;
  grid-column: 1 / 2;
}

@media screen and (max-width: 1027px) {
  .wrapper {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto auto auto;
    grid-gap: 20px;
   margin-top: 20px; 

  }
  
  .mainVideo {
    grid-row: 1 / 2;
    grid-column: 1 / 1;
   }
   
   .videosList {
    grid-row: 2 / 3;
    grid-column: 1 / 1;
   }
   
   .comments {
     grid-row: 3 / 4;
     grid-column: 1 / 1;
   }
}
.title {
  padding: 20px 0 12px 0;
  border-bottom: solid 1px lightgray;
}

.title-video {
  font-family: Roboto, Arial, sans-serif;
  font-weight: 400;
  color: #0a0a0a;
  font-size: 18px;
}

.viewCount {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}

.viewCount div {
  text-align: center;
  font-family: Roboto, Arial, sans-serif;
  font-weight: 400;
  color: #0a0a0a;
  font-size: 16px;
}

.video-description img {
  float: left;
  height: 70px;
  width: 70px;

  border-radius: 35px;

}

.video-description {
  padding: 10px 0;
  border-bottom: solid 1px lightgray;
}

.video-description .channel-title {
  font-family: Roboto, Arial, sans-serif;
  font-weight: bold;
  color: #0a0a0a;
  font-size: 0.9em;
  padding-left: 80px;
  margin-top: 15px;
}

.video-description .published-at {
  font-family: Roboto, Arial, sans-serif;
  font-weight: 400;
  color: #606060;
  font-size: 0.8em;
  padding-left: 80px;
  margin-bottom: 15px; 
}

.video-description .description {
  padding-left: 80px;
}

.video {
  position:relative;
  padding-bottom:56.25%;
  padding-top:25px;
  height:0;


}

.video iframe {
  position:absolute;
  top:0;
  left:0;
  width:100%;
  height:100%;
  /* max-width: 780px;
  max-height: 405px; */
}

.comment-count {
  margin: 10px 0 0 0;
  padding-top: 10px;
  font-family: Roboto, Arial, sans-serif;
  font-weight: 300;
  color: #0a0a0a;
  font-size: 16px; 
  border-top: 1px solid lightgray;
}




input {
  max-width: 500px;
  
}

.form-control {
  min-width: 150px;
  border-radius: 0;
}

.input-group {
  width: 50%;
  margin: 0 auto;
  
}

.fa-search {
  width: 50px;

}

@media screen and (max-width: 767px) {
  .input-group {
    width: 100%;
    margin: 0 auto;
  }
}
.img-video {
  float: left;
  
}

.img-video img {
  height: 100px;
  cursor: pointer;
}

.name-video {
  font-size: 10px;
}

.name-video h3 {
  margin-left: 185px;
  font-family: Roboto, Arial, sans-serif;
  font-weight: bold;
  color: #0a0a0a;
  font-size: 1.17em;
  cursor: pointer;
  
 
}

.name-channel {
  margin-left: 185px;
  font-family: Roboto, Arial, sans-serif;
  font-weight: 400;
  color: #606060;
  font-size: 0.8em;
  cursor: pointer;
  
}

.list-group-item {
  padding: 0 0 20px 0;
  border: none;
}


.comment {
  margin-bottom: 25px;
}

.comment img {
  float: left;
  height: 40px;
  width: 40px;
  border-radius: 50%;
}

.comment p {
  padding-left: 55px;
}

.comment .comment-author {
  font-family: Roboto, Arial, sans-serif;
  font-weight: bold;
  color: #0a0a0a;
  font-size: 12px;
  margin-bottom: 0px;
}

.comment .comment-published-at {
  padding-left: 10px;
  font-family: Roboto, Arial, sans-serif;
  font-weight: 400;
  color: #606060;
  font-size: 12px;
}

.comment .commentBody {
  font-family: Roboto, Arial, sans-serif;
  font-weight: 500;
  color: #0a0a0a;
  font-size: 12px;
   
}

.no-comments {
  font-weight: 600;
}
@keyframes lds-spinner {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@-webkit-keyframes lds-spinner {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.lds-spinner {
  position: relative;
  margin: 100px auto;
}
.lds-spinner div {
  left: 96px;
  top: 31px;
  position: absolute;
  -webkit-animation: lds-spinner linear 1.7s infinite;
  animation: lds-spinner linear 1.7s infinite;
  background: #ff0000;
  width: 8px;
  height: 42px;
  border-radius: 36%;
  -webkit-transform-origin: 4px 69px;
  transform-origin: 4px 69px;
}
.lds-spinner div:nth-child(1) {
  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-animation-delay: -1.586666666666667s;
  animation-delay: -1.586666666666667s;
}
.lds-spinner div:nth-child(2) {
  -webkit-transform: rotate(24deg);
  transform: rotate(24deg);
  -webkit-animation-delay: -1.473333333333333s;
  animation-delay: -1.473333333333333s;
}
